.flex-container_div{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.div_process_title {
    margin-left: 0px;
    font-size: 17px;
    font-weight: 600;
    color: #111111;
}
.flex_processs {
    text-align: center;
    height: 32px;
    line-height: 32px;
    width: 100%;
    /*padding: 12px;*/
}
.flex-container_div>:first-child>:first-child{
    border-right: 1px solid #D9D9D9;
}
.flex-container_div>:first-child>:last-child{
    border-right: 1px solid #D9D9D9;
}

.div_process{
    padding: 2px 0px 2px 0px;
    width: calc(100% - 30px);
    margin-left: 15px;
    background-color: white;
}
.flex_processs>:first-child{
    font-size: 24px;
    text-align: center;
    line-height: 28px;
}
.flex_processs>:last-child{
    font-size: 14px;
    color: #9B9B9B;
    line-height: 28px;
}
.div_underline_bottom1{
    /*margin-top: 16px;*/
    -webkit-box-shadow: 0 -1px 0 0 #E5E5E5;
            box-shadow: 0 -1px 0 0 #E5E5E5
}


