.am-wingblank {
    background-color: white !important;
}

.box {
    width: 600px;
    height: 300px;
    border: 1px solid #000000;
}

.circle {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: blue;
    margin-top: 100px;
    -webkit-animation-name: circleRun;
            animation-name: circleRun;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    /*animation-direction: alternate;*/
}

.box:hover .circle {
    /*宽度减去小球的直径 550px*/
}

@-webkit-keyframes circleRun {
    20% {
        -webkit-transform: translate(150px, 75px);
                transform: translate(150px, 75px);
    }
    35% {
        -webkit-transform: translate(400px, -75px);
                transform: translate(400px, -75px);
    }
    50% {
        -webkit-transform: translate(550px, 0px);
                transform: translate(550px, 0px);
        background-color: red;
    }
    65% {
        -webkit-transform: translate(400px, 75px);
                transform: translate(400px, 75px);
    }
    80% {
        -webkit-transform: translate(150px, -75px);
                transform: translate(150px, -75px);
    }
    to {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        background-color: blue;
    }
}

@keyframes circleRun {
    20% {
        -webkit-transform: translate(150px, 75px);
                transform: translate(150px, 75px);
    }
    35% {
        -webkit-transform: translate(400px, -75px);
                transform: translate(400px, -75px);
    }
    50% {
        -webkit-transform: translate(550px, 0px);
                transform: translate(550px, 0px);
        background-color: red;
    }
    65% {
        -webkit-transform: translate(400px, 75px);
                transform: translate(400px, 75px);
    }
    80% {
        -webkit-transform: translate(150px, -75px);
                transform: translate(150px, -75px);
    }
    to {
        -webkit-transform: translate(0px, 0px);
                transform: translate(0px, 0px);
        background-color: blue;
    }
}


