.flex_taskbox {
    text-align: center;
    height: 32px;
    line-height: 32px;
    width: 100%;
    margin-top: 18px;
}

.tabs_num {
    font-size: 30px;
    font-weight: 600;
}

.div_task_title {
    font-size: 14px;
    color: #000000;
    text-align: center;
    margin-top: -8px;
}
.div_tabs_update{
    background-color: white;
}

.div_tabs_update > .am-tabs > .am-tabs-tab-bar-wrap > .am-tabs-default-bar > .am-tabs-default-bar-content {
    height: 8px !important;
    background-color: #FFFFFF !important;
    border: 0px !important;
}

.div_tabs_update > .am-tabs > .am-tabs-tab-bar-wrap > .am-tabs-default-bar > .am-tabs-default-bar-content > .am-tabs-default-bar-underline {
    border: none !important;
}

.div_tabs_update > .am-tabs > .am-tabs-tab-bar-wrap > .am-tabs-default-bar > .am-tabs-default-bar-content > :first-child {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    height: auto !important;
    border: 0px !important;
    background-color: white !important;
    padding: 0px;
    margin-top: -12px;
    width: 50% !important;
}

.div_tabs_update > .am-tabs > .am-tabs-tab-bar-wrap > .am-tabs-default-bar > .am-tabs-default-bar-content > :nth-child(2) {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: start;
    margin-right: 1px;
    height: auto !important;
    border: 0px !important;
    background-color: white !important;
    padding: 0px;
    margin-top: -12px;
    margin-left: -2px;
    width: 50% !important;
}

.am-tabs-default-bar-bottom .am-tabs-default-bar-tab::before {
    content: '';
    position: absolute;
    background-color: #fff !important;
}



