.span_listbox_title{
    font-size: 17px;
    color: #000000;
    letter-spacing: 0;
    font-weight: 600;
}
.div_list_brief{
    font-size: 16px;
    color: #424242;
}

.am-list-body::before {
    content: '';
    position: absolute;
    background-color: transparent !important;
    display: block;
    z-index: 1;
    top: 0;
    right: auto;
    bottom: auto;
    left: 0;
    width: 100%;
    height: 1PX;
    -webkit-transform-origin: 50% 50%;
    -ms-transform-origin: 50% 50%;
        transform-origin: 50% 50%;
    -webkit-transform: scaleY(0.5);
    -ms-transform: scaleY(0.5);
        transform: scaleY(0.5);
}


