.div_tasktools{
        padding: 8px;
}
.div_tasktools>div{
        border: 1px solid #2979D0;
        border-radius: 4px;
        height:36px;
        line-height: 36px;
        text-align: center;
        width:70px;
        margin-right: 8px;
        font-size: 13px;
        color: #2979D0;
}
.div_tasktools>:last-child{
        background: #2979D0;
        color:white;
}
.div_tasktools>div>img{
        width:18px;
        height: 14px;
        margin-top: 10px;
}
.div_tasktools>div>span{
        margin-left: 6px;
}


