.div_pageloading {
    position: fixed;
    top: 36%;
    right: calc((100% - 20px) / 2);
    z-index: 2019;
}

.bg_loading {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: block;
    background-color: white;
    z-index: 1000;
    opacity: 0.3;
}



