.div_carousel_tip{
    font-size: 8px;
    color: #FFFFFF;
    background: rgba(0,0,0,0.5);
    text-align: left;
    zoom: 2;
    width: 100%;
    position: absolute;
    height: 14px;
    line-height: 14px;
}
.div_carousel_tip>:first-child{
    margin-left: 8px;
}
.div_carousel_tip>:last-child{
    float: right;
    margin-right: 8px;
}
.div_carousel_tip>:last-child>:first-child{
    font-size: 14px;
    font-weight: bold;
}




