.div_hend {
    margin-left: 16px;
}

.div_hend > :first-child {
    font-size: 17px;
    color: #000000;
    letter-spacing: 0;
    margin-top: 4px;
}

.div_hend > :last-child {
    font-size: 14px;
    color: #888888;
    margin-top: 12px;
}

.div_headtip {
    height: 52px;
    line-height: 52px;
    font-size: 14px;
    color: #999999;
    text-align: left;
}

.div_metter_header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: white;
    height: 32px;
    line-height: 32px;
    font-size: 17px;
    color: #000000;
    font-weight: 500;
    padding: 8px 15px;
}

.div_metter_header > :last-child {
    color: #1890FF
}

.am-list-content > span {
    font-size: 16px;
    color: #999999;
}

.am-list-extra {
    -ms-flex-preferred-size: 36%;
    flex-basis: 50% !important;
}

.am-list-extra > div {
    font-size: 16px;
    color: #424242;
}

.am-list-brief {
    overflow: auto !important;
    white-space: pre-line !important;;
}

.div_taskinfos_title {
    font-size: 14px;
    color: #999999 !important;
}

.div_taskinfos_text {
    font-size: 16px;
    color: #424242 !important;
}

.div_fontbold {
    font-size: 17px;
    font-weight: bold;
    color: #000000;
}

.div_file {
    margin-top: 12px;
    background: #FAFAFA;
    border: 1px solid #E0E0E0;
    padding: 8px;
    font-size: 14px;
    display: -ms-flexbox;
    display: flex;
}

.div_file > img {
    width: 48px;
    height: 48px;
    margin-right: 12px;
}

.div_openFile {
    margin-top: 8px;
    font-size: 14px;
    color: #1890FF;
}

.div_feedback_msg {
    margin-top: 12px;
    border-top: 1px solid #E0E0E0;
    padding: 8px;
    font-size: 16px;
    color: #424242;
}

.div_feedback_msg > div {
    margin-top: 6px;
}

.div_circle {
    margin-top: -2px;
    margin-right: 6px;
    /*width:20px;*/
    /*height:20px;*/
    border-radius: 10px;
    background-color: #CCCCCC;
    padding: 7px;
}

.div_circle > :first-child {
    width: 6px;
    height: 6px;
    border-radius: 3px;
    background-color: white;
}




