.div_piece_code {
    font-size: 14px;
    color: #9E9E9E;
    -webkit-box-shadow: 0 -1px 0 0 #E5E5E5, inset 0 -1px 0 0 #E5E5E5;
            box-shadow: 0 -1px 0 0 #E5E5E5, inset 0 -1px 0 0 #E5E5E5;
}

.div_piece_code > .am-flexbox {
    padding: 12px
}

.div_piece_title {
    width: 210px;
    margin-left: 12px;
    font-size: 17px;
    color: #181818;
    overflow: auto;
    word-wrap: break-word;
}
.div_piece_state{
    background: #00CBBC;
    border-radius: 24px;
    font-size: 12px;
    color:white;
    width:76px;
    height: 22px;
    line-height: 22px;
    text-align: center;
}


