.div_unitview {
    padding: 8px 12px 8px 12px;
}

.div_unit_flex {
    padding: 12px 32px 12px 8px;
    -webkit-box-shadow: inset 0 -1px 0 0 #D9D9D9;
            box-shadow: inset 0 -1px 0 0 #D9D9D9;
}

.div_unit_flex > :first-child {
    font-size: 14px;
    color: #424242;
}

.div_unit_flex > :last-child {
    font-size: 14px;
    color: #0270E0;
}

.span_tools > img {
    width: 16px;
    height: 10px;
}
.span_tools>:first-child{
    margin-bottom: 5px;
}
.span_tools>:last-child{
    margin-bottom: -4px;
    margin-left: -16px;
}

.div_unit_tip{
    background-color: #F4F4F4;
    padding-right: 0;
}
.div_unit_tip>div{
    color:#9E9E9E !important;
}


